___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --ratio: 16 / 9;
  --add-project-form-height: max-content;
  --add-project-form-width: 700px;
  --priority-color-low: rgb(39, 206, 111);
  --priority-color-medium: rgb(255, 213, 0);
  --priority-color-high: rgb(239, 39, 99);
  --status-done-foreground: rgba(0, 0, 0, 0.164);
}

/* Reset from Josh W. Comeau */
/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

html {
  font-size: 16px;
  font-family: "Noto Sans", serif;
}

body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
  background-color: #ffffff;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-attachment: fixed;
  background-size: cover;
  height: 100vh;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  display: grid;
  grid-template-columns: 20% 1fr;
  grid-template-rows: max-content 1fr max-content;
  grid-template-areas: "banner banner" "nav todo-body" "footer footer";
  width: 1250px;
  height: 90%;
  background-color: #ffffff;
  -webkit-box-shadow: 0px 10px 32px -2px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 10px 32px -2px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 10px 32px -2px rgba(0, 0, 0, 0.75);
}

.banner {
  display: flex;
  grid-area: banner;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 40px;
  background-color: rgb(131, 216, 180);
  color: #ffffff;

  p {
    font-size: clamp(2rem, 3vh, 3rem);
    font-weight: 800;
  }

  .menu {
    opacity: 0;
    user-select: none;
  }
}

footer {
  grid-area: footer;
  text-align: center;
  font-size: 1rem;
  font-weight: 500;
  background-color: black;
  color: #ffffff;
  padding: 10px 0px;
}

.nav-bar {
  min-height: 100%;
  grid-area: nav;
  background-color: #e6f8eb;
  padding: 20px 20px;
  display: grid;
  grid-template-rows: max-content 1fr max-content;
  grid-template-areas: "default" "custom" "add-project";
  overflow: auto;
  row-gap: 15px;
}

.project-name-container {
  display: flex;
  flex-direction: column;
  padding: 20px 0px;
}

.project-name-container.default-section {
  grid-area: default;
  padding-top: 0px;
  border-bottom: 3px solid grey;
}

.project-name-container.custom-section {
  grid-area: custom;
  overflow: auto;
  min-height: 20%;
  padding-top: 0px;
}

.project-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-item,
.btn {
  border-radius: 8px;
}

.project-item.type-default,
.custom-section-header,
.add-project,
.add-todo {
  font-size: clamp(1rem, 1.5vw, 1.3rem);
  font-weight: 500;
}

.project-item.type-custom {
  font-size: clamp(0.9rem, 1.4vh, 1.1rem);
  padding: 10px 10px 10px 20px;
}

.project-item.type-default,
.custom-section-header {
  padding: 10px;
}

.project-item.selected {
  background-color: #cee4d4;
  border: 2px solid #83d8b4;
}

.project-item#Home {
  order: 1;
}

.project-item#Today {
  order: 2;
}

.project-item#Important {
  order: 3;
}

.project-item#Task {
  order: 4;
}

.edit,
.delete,
.expand,
.shrink,
.menu {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  border-radius: 0px;
  border: none;
  padding: 0;
  width: clamp(18px, 2vw, 23px);
  height: clamp(18px, 2vw, 23px);

  svg {
    display: block;
    width: clamp(18px, 2vw, 23px);
  }
}

.delete svg:hover,
.todo.done .delete svg:hover {
  fill: var(--priority-color-high);
}

.edit svg:hover path,
.expand svg:hover path,
.todo.done .expand svg:hover path,
.shrink svg:hover path,
.todo.done .shrink svg:hover path {
  stroke: var(--priority-color-low);
}

.add-project,
.add-todo,
button[type="submit"],
.btn.cancel,
.btn.close-form {
  background: transparent;
  color: #ffffff;
  border: none;
  padding: 8px;
  font-weight: 700;
}

.add-project,
.add-todo,
button[type="submit"] {
  background-color: rgb(131, 216, 180);
}

.add-project:hover,
.add-todo:hover,
button[type="submit"]:hover {
  cursor: pointer;
  background-color: rgb(117, 195, 162);
}

.btn.cancel {
  background-color: rgb(216, 131, 156);
}

.btn.cancel:hover {
  cursor: pointer;
  background-color: rgb(195, 117, 141);
}

.project-item:hover {
  cursor: pointer;
  background-color: #ffffff;
}

.todo-container {
  grid-area: todo-body;
  display: flex;
  flex-direction: column;
  overflow: auto;
  padding: 20px 40px;
  gap: 20px;
}

.todo {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: max-content;
  background-color: #edebeb;
  padding: 15px;
  transform: translateY(0px); /* Add this line */
  transition: transform 0.2s ease; /* Add this line for smooth transition */
  border-radius: 5px;
  row-gap: 15px;
  font-size: clamp(0.8rem, 2.5vw, 1rem);

  .todo-progress {
    display: flex;
    align-items: center;
  }

  input[type="checkbox"] {
    width: clamp(18px, 2vw, 23px);
    height: clamp(18px, 2vw, 23px);
  }
}

.todo > * {
  display: flex;
  align-items: center;
}

.todo-title {
  max-width: 40ch;
  overflow: auto;
}

.todo-title,
.todo-detail .label {
  font-weight: 600;
}

.todo-detail {
  display: flex;

  .label {
    min-width: max-content;
    margin-right: 10px;
  }
}

.todo.done {
  .todo-detail > * {
    color: var(--status-done-foreground);
  }

  .delete svg {
    fill: var(--status-done-foreground);
  }

  .todo-title,
  .todo-description,
  .todo-due-date {
    text-decoration: line-through;
    color: var(--status-done-foreground);
  }

  .edit {
    cursor: not-allowed;
    user-select: none;
  }

  .edit svg path,
  .expand svg path,
  .shrink svg path {
    stroke: var(--status-done-foreground);
  }
}

.todo-left-cont {
  gap: 20px;
}

.todo-right-cont {
  gap: 30px;
}

.todo-expanded-cont {
  display: block;
  width: 100%;
  padding-top: 10px;
  border-top: 2px solid lightgrey;
}

.project-item:hover,
.todo:hover {
  -webkit-box-shadow: 6px 7px 9px -3px rgba(97, 93, 97, 1);
  -moz-box-shadow: 6px 7px 9px -3px rgba(97, 93, 97, 1);
  box-shadow: 6px 7px 9px -3px rgba(97, 93, 97, 1);
  transform: translateY(-3px); /* Add this line */
  transition: transform 0.3s ease; /* Add this line for smooth transition */
}

.todo.priority-low {
  border-left: 5px solid var(--priority-color-low);
}

.todo.priority-medium {
  border-left: 5px solid var(--priority-color-medium);
}

.todo.priority-high {
  border-left: 5px solid var(--priority-color-high);
}

.add-todo {
  order: 2;
  width: 30%;
  align-self: center;
}

.add-project-form {
  display: flex;
  flex-direction: column;
  gap: 10px;

  .btn-cont {
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
  }
}

.add-project-form input {
  padding: 5px;
  border-radius: 5px;
  border: transparent;
}

input[type="text"]:focus {
  border: transparent;
  outline: none;
}

input[type="text"]::placeholder {
  font-style: italic;
}

.btn-cont .btn {
  flex: 1;
}

.add-todo-form-container {
  position: absolute;
  border: 2px solid grey;
  border-radius: 5px;

  .banner {
    border-radius: 5px 5px 0px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .menu {
      opacity: 0;
      user-select: none;
    }
  }

  .banner button {
    background-color: transparent;
    border: none;
    color: #ffffff;
    font-size: 1.8rem;
    font-weight: 800;
    padding: 5px;
    margin-right: 5px;
  }

  .banner button:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}

.add-todo-form {
  border-radius: 0px 0px 5px 5px;
  background-color: #ffffff;
  height: var(--add-project-form-height);
  width: var(--add-project-form-width);
  opacity: 0;
  padding: 30px;
  display: grid;
  grid-template-areas: "title title" "description description" "priority duedate" "submit submit";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(4, max-content);
  gap: 15px;
}

.add-todo-form.active {
  opacity: 1;
}

.blur-overlay.active {
  position: absolute;
  backdrop-filter: blur(15px);
  height: 100%;
  width: 100%;
}

.entry,
.priority-options {
  padding: 5px;
}

.title > input,
.description > input {
  width: 100%;
  border: transparent;
}

.title > input:focus,
.description > input:focus {
  border: transparent;
}

.title {
  grid-area: title;
  font-size: 1.6rem;
  font-weight: 800;
}

.description {
  grid-area: description;
  font-size: 1.1rem;
}

.priority-options {
  grid-area: priority;
  border: none;
  display: flex;
  font-size: 1.1rem;
  gap: 20px;
}

label[for="low"] {
  color: var(--priority-color-low);
}

label[for="medium"] {
  color: var(--priority-color-medium);
}

label[for="high"] {
  color: var(--priority-color-high);
}

.due-date {
  grid-area: duedate;
  display: flex;
  flex-direction: column;
  gap: 2px;
  font-size: 1.1rem;
  width: max-content;
}

.due-date > input {
  border: transparent;
}

legend,
.due-date label {
  font-weight: 700;
}

.submit-todo {
  grid-area: submit;
  font-size: 1.3rem;
  width: 30%;
  justify-self: center;
  margin-top: 20px;
}

@media screen and (max-width: 1050px) {
  .main {
    height: 100%;
    background-color: #ffffff;
    display: grid;
    grid-template-areas: "banner" "body" "footer";
    grid-template-rows: max-content 1fr max-content;
    grid-template-columns: 1fr;
  }

  .banner {
    display: flex;
    grid-area: banner;
    justify-content: space-between;
    align-items: center;
    padding: 10px 40px;
    background-color: rgb(131, 216, 180);
    color: #ffffff;
    p {
      font-size: clamp(1.5rem, 2.3vw, 2.5rem);
      font-weight: 800;
    }

    .menu {
      opacity: 1;
    }

    .menu:hover {
      path {
        stroke: black;
      }
    }
  }

  footer {
    text-align: center;
    grid-area: footer;
    font-size: clamp(0.8rem, 1vh, 1rem);
    font-weight: 500;
    background-color: black;
    color: #ffffff;
    padding: 10px 0px;
  }

  .nav-bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: -1;

    .btn.add-project {
      justify-self: center;
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  .nav-bar .custom-section .type-section .btn.delete {
    opacity: 1;
  }

  .nav-bar.active {
    position: relative;
    grid-area: body;
    z-index: 1;
    opacity: 1;
  }

  .todo-container {
    grid-area: body;
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media screen and (max-width: 650px) {
  .add-todo-form-container {
    width: 100%;
    .add-todo-form {
      width: 100%;
      display: flex;
      flex-direction: column;
    }

    .add-todo-form .btn.submit-todo {
      align-self: center;
      font-size: 1.1rem;
    }
  }
}
