22' points='1080 300 990 450 1170 450'/%3E%3Cpolygon fill='%23FFF' points='1080 300 1170 150 990 150'/%3E%3Cpolygon points='1080 600 990 750 1170 750'/%3E%3Cpolygon fill='%23666' points='1080 600 1170 450 990 450'/%3E%3Cpolygon fill='%23DDD' points='1080 900 1170 750 990 750'/%3E%3C/g%3E%3C/pattern%3E%3C/defs%3E%3Crect x='0' y='0' fill='url(%23a)' width='100%25' height='100%25'/%3E%3Crect x='0' y='0' fill='url(%23b)' width='100%25' height='100%25'/%3E%3C/svg%3E\");\n  background-attachment: fixed;\n  background-size: cover;\n  height: 100vh;\n}\n\n/* 5. Improve media defaults */\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n  display: block;\n  max-width: 100%;\n}\n\n/* 6. Inherit fonts for form controls */\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\n/* 7. Avoid text overflows */\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}\n\n/* 8. Improve line wrapping */\np {\n  text-wrap: pretty;\n}\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  text-wrap: balance;\n}\n\n/*\n  9. Create a root stacking context\n*/\n#root,\n#__next {\n  isolation: isolate;\n}\n\nbody {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.main {\n  display: grid;\n  grid-template-columns: 20% 1fr;\n  grid-template-rows: max-content 1fr max-content;\n  grid-template-areas: \"banner banner\" \"nav todo-body\" \"footer footer\";\n  width: 1250px;\n  height: 90%;\n  background-color: #ffffff;\n  -webkit-box-shadow: 0px 10px 32px -2px rgba(0, 0, 0, 0.75);\n  -moz-box-shadow: 0px 10px 32px -2px rgba(0, 0, 0, 0.75);\n  box-shadow: 0px 10px 32px -2px rgba(0, 0, 0, 0.75);\n}\n\n.banner {\n  display: flex;\n  grid-area: banner;\n  justify-content: flex-start;\n  align-items: center;\n  padding: 20px 40px;\n  background-color: rgb(131, 216, 180);\n  color: #ffffff;\n\n  p {\n    font-size: clamp(2rem, 3vh, 3rem);\n    font-weight: 800;\n  }\n\n  .menu {\n    opacity: 0;\n    user-select: none;\n  }\n}\n\nfooter {\n  grid-area: footer;\n  text-align: center;\n  f